import { css } from "styled-components"

export const mobile = (props) => {
    return css`
    @media only screen and (max-width: 420px) {
        ${props}
    }
    `;
}

export const iphone10 = (props) => {
    return css`
    @media only screen and (max-width: 420px) {
        ${props}
    }
    `;
}